import React from "react";
import { MaskedInput } from "grommet";

const Component = ({ onChange, value, placeholder, ...props }) => {
  return (
    <MaskedInput
      {...props}
      mask={[
        {
          length: [1, 5],
          regexp: /^[+-]?\d+(\.\d+)?$/,
          placeholder: "Price",
        },
      ]}
      value={value}
      onChange={(event) => {
        onChange && onChange(Number(event.target.value));
      }}
    />
  );
};

export default Component;
