import React, { useState, useCallback, useEffect } from "react";
import { Box, Button, TextInput, Heading, Image, CheckBox } from "grommet";
import { useDropzone } from "react-dropzone";
import Parse from "../../library/parse";


const Component = ({
  initialParseFile,
  onChange
}) => {
  
    const [fileObject, setFileObject] = useState(initialParseFile);

  
    const onDrop = useCallback(
    (acceptedFiles) => {
      const uploadFile = (file) => {
        var parseFile = new Parse.File(file.name, file);
        parseFile.save().then((parseFile) => {
            setFileObject(parseFile.toJSON())
            onChange && onChange(parseFile.toJSON())
        });
      };
      const file = acceptedFiles && acceptedFiles[0];
      uploadFile(file);
    },
    []
  );
  const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop });

  return (
    <>
    {
        fileObject && fileObject.url && <Box 
            height="small" 
            width="small"
            direction="row"
            justify="center"
        >
        <Image
        fit="cover"
        src={fileObject.url}
        />
        </Box>  
    }

    <Box align="center" justify="center" direction="row" fill={true} background="accent-2">
    <div {...getRootProps()} style={{ textAlign: 'center', width: '100%'}}>
      <input {...getInputProps()} />
      <>
      {
          isDragActive ? <p>Yes, Drop the files here ...</p> : <p>Drop the files here ...</p>
      }
      
      </>
    </div>
    </Box>
    </>
  );
};

export default Component;
