import React, { useEffect, useState } from "react";
import NumberTextInput from "./NumberTextInput";
import Rte from "../../components/Rte";
import DateTimePicker from "../../components/DateTimePicker";
import { Box, TextInput, Button, Heading, Image, CheckBox } from "grommet";
import { parseISO } from "date-fns";
import DropZone from '../../components/DropZone'


const Home = ({ columns, mode, onClick, model, suffix = "" }) => {
  const [formModel, setFormModel] = useState({});
  const [uploadFileObject, setUploadFileObject] = useState(null)

  useEffect(() => {
    setFormModel({
      ...formModel,
      ...uploadFileObject
    })

  }, [uploadFileObject])

  useEffect(() => {
    if (mode === "update") {
      const m = columns.reduce(
        (p, n) => {
          const key = n.property;
          p[key] = model[key];
          return p;
        },
        {
          objectId: model["objectId"],
        }
      );
      setFormModel(m);
    }
    if (mode === "delete") {
      setFormModel({
        objectId: model["objectId"],
      });
    }

    if (mode === "create") {
      setFormModel({});
    }
  }, [mode, columns, model]);
  return (
    <Box>
      <Heading> {mode} </Heading>
      {columns.map((e) => {
        let component;

        if (e.type === "File") {
          component = (
            <>
            {
              JSON.stringify(formModel)
            }
            <DropZone
              placeholder={e.headerText}
              initialParseFile={formModel[e.property]}
              onChange={(file) =>
                setUploadFileObject({
                  [e.property]: file
                })
              }
            />
            </>
          );
        }

        if (e.type === "NumberTextInput") {
          component = (
            <NumberTextInput
              placeholder={e.headerText}
              value={formModel[e.property]}
              onChange={(value) =>
                setFormModel({
                  ...formModel,
                  [e.property]: value,
                })
              }
            />
          );
        }
        if (e.type === "Textarea") {
          component = (
            <Rte
              placeholder={e.headerText}
              html={formModel[e.property]}
              onChange={(v) =>
                setFormModel({
                  ...formModel,
                  [e.property]: v,
                })
              }
            />
          );
        }

        if (e.type === "DateTimePicker") {
          const dtIso =
            formModel[e.property] && formModel[e.property].iso
              ? formModel[e.property].iso
              : Object.prototype.toString.call(formModel[e.property]) ===
                "[object Date]"
              ? formModel[e.property]
              : new Date().toISOString();
          component = (
            <DateTimePicker
              value={parseISO(dtIso)}
              onChange={(value) =>
                setFormModel({
                  ...formModel,
                  [e.property]: value,
                })
              }
            />
          );
        }
        if (e.type === "Image") {
          component = <Image src={e.url} />;
        }

        if (e.type === "CheckBox") {
          component = (
            <CheckBox
              checked={formModel[e.property]}
              onChange={(event) =>
                setFormModel({
                  ...formModel,
                  [e.property]: event.target.checked,
                })
              }
            />
          );
        }
        if (!component) {
          component = (
            <TextInput
              placeholder={e.headerText}
              value={formModel[e.property]}
              onChange={(event) =>
                setFormModel({
                  ...formModel,
                  [e.property]: event.target.value,
                })
              }
            />
          );
        }
        return (
          <>
            {e.headerText}
            {component}
          </>
        );
      })}
      <Button
        label={mode}
        onClick={() => {
          onClick && onClick(mode, formModel);
        }}
      />
    </Box>
  );
};

export default Home;
